.sidebar {
    position: fixed;
    top: 0;
    left: -250px;
    width: 250px;
    height: 100%;
    background: #0078d7;
    color: white;
    transition: left 0.3s ease;
    z-index: 1000;
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .sidebar .close-btn {
    padding: 10px;
    text-align: right;
    cursor: pointer;
  }
  
  .sidebar nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar nav ul li {
    padding: 15px 20px;
  }
  
  .sidebar nav ul li a {
    color: white;
    text-decoration: none;
    display: block;
  }
  
  .menu-icon {
    font-size: 24px;
    cursor: pointer;
    margin-right: 10px;
    display: inline-block;
    color: white;
  }
  