.chatbot {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000;
}

.chatbot-toggle {
    background-color: #0078d7;
    color: white;
    padding: 10px 15px;
    border-radius: 50%;
    cursor: pointer;
    font-size: 20px;
    text-align: center;
}

.chatbot-window {
    width: 300px;
    height: 400px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: fixed;
    bottom: 80px;
    right: 20px;
}

.chatbot-header {
    background-color: #0078d7;
    color: white;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}

.chatbot-messages {
    flex: 1;
    padding: 10px;
    overflow-y: auto;
    background-color: #f9f9f9;
}

.chatbot-message {
    margin: 5px 0;
    padding: 10px;
    border-radius: 5px;
    max-width: 80%;
}

.chatbot-message.user {
    background-color: #0078d7;
    color: white;
    align-self: flex-end;
}

.chatbot-message.bot {
    background-color: #ddd;
    align-self: flex-start;
}

.chatbot-input {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ddd;
    background: #fff;
}

.chatbot-input input {
    flex: 1;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-right: 5px;
}

.chatbot-input button {
    background-color: #0078d7;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 8px 10px;
    cursor: pointer;
}
